          "15",
          "16",
          "17",
          "18",
          "19",
          "20",
          "21",
          "22",
          "23",
        ],
        series: [
          100, 200, 300, 700, 1200, 1600, 1800, 1700, 1900, 1800, 1600, 1400,
          1500, 1400, 1300, 1200, 1100, 900, 500, 400, 350, 200, 150, 100,
        ],
      },
    };
  },
  methods: {
    createData(id, xAxis, series, unit) {
      var chartDom = document.getElementById(id);
      var myChart = this.$echarts.init(chartDom);
      var option;
      option = {
        grid: {
          top: "15%",
          left: "15%",
          right: "5%",
          bottom: "15%",
        },
        tooltip: {
          trigger: "axis",
        },
        xAxis: {
          data: xAxis,
          boundaryGap: false,
          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            interval: 1,
            color: "#fff",
          },
          axisTick: {
            //坐标轴刻度线
            show: false,
          },
        },
        yAxis: {
          name: `(${unit})`,
          max: function (value) {
            return value.max * 1.2;
          },
          nameTextStyle: {
            color: "#fff",
            fontSize: 12,
          },
          type: "value",
          splitLine: {
            show: false, //去掉网格线
          },

          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            color: "#fff",
          },
        },
        series: [
          {
            data: series,
            smooth: true,
            type: "line",
            lineStyle: {
              width: 1,
              color: "#d6f478",
            },
            itemStyle: {
              normal: {
                color: "#d6f478",
              },
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData(
      "list_01",
      this.rightData.xAxis,
      this.rightData.series,
      this.rightData.unit
    );
    this.createData(
